@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:Poppins, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#32363e}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.app{position:relative}.app .errMessage{font-size:14px;background:#fff;padding:1em;color:#f44336;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:20px auto;border-radius:10px;-webkit-animation:message 300ms;animation:message 300ms;transform-origin:top}@-webkit-keyframes message{from{opacity:0;transform:scale(0)}to{opacity:1;transform:scale(1)}}@keyframes message{from{opacity:0;transform:scale(0)}to{opacity:1;transform:scale(1)}}nav{display:flex;align-items:center;justify-content:center;background:#282c34;padding:20px 10px;width:100%}nav form{display:block;margin:0;width:100%;max-width:450px;display:flex}nav form input,nav form button{font-family:inherit;font-size:14px;padding:10px;outline:none;border-radius:10px;transition:300ms;display:inline-block}nav form input{flex-grow:1;margin-right:-30px;border:none;background:rgba(240,240,240,0.15);color:#fff}nav form input:focus{background:rgba(255,255,255,0.16)}nav form input::-webkit-input-placeholder{color:rgba(255,255,255,0.7)}nav form input:-ms-input-placeholder{color:rgba(255,255,255,0.7)}nav form input::-ms-input-placeholder{color:rgba(255,255,255,0.7)}nav form input::placeholder{color:rgba(255,255,255,0.7)}nav form button{border:none;padding:12px 18px;cursor:pointer;font-weight:500;background:#fff;color:#282c34}nav form button[disabled]{cursor:none !important;background:#dfdfdf;color:#b1b1b1}.row{color:#fff;display:grid;grid-template-columns:1fr 1fr;grid-gap:70px;align-items:center;max-width:768px;margin:20px auto}@media (max-width: 520px){.row{grid-gap:10px;grid-template-columns:auto;text-align:center}.row .location{display:flex;justify-content:center}.row .location .city{margin-right:10px}.row .weather{justify-content:center}}.row .location{text-align:right}.row .location .city{font-size:10vmin;line-height:1}.row .location .country{font-size:6vmin}.row .climate .temp{font-size:15vmin;position:relative}.row .climate .temp p{font-weight:700;margin:0 0 -1em;display:inline-block}.row .climate .temp span{font-size:3vmin}.row .climate .feelsLike{font-size:18px;margin-bottom:16px}.row .climate .weather{text-transform:uppercase;font-weight:500;font-size:14px;display:flex;align-items:center;color:rgba(255,255,255,0.5);height:40px}.row .climate .weather img{margin-left:10px;max-width:40px;max-height:40px;display:block}.flex{margin-top:1em;display:flex;align-items:center;justify-content:center;color:rgba(255,255,255,0.8)}@media (max-width: 600px){.flex p{margin:0 0.7em 0}.flex p span{display:none}}.flex p{display:flex;align-items:center;margin:0 0.5em}.flex p svg{margin-right:3px;max-width:25px;max-height:25px;fill:rgba(255,255,255,0.8)}.warning{text-align:center;margin:5vw;color:#fff;pointer-events:none}.warning svg,.warning img{width:180px;max-width:70%;pointer-events:none}.warning p{opacity:0.9;font-weight:300;font-size:1.15em}@media (max-width: 768px){.warning p{font-size:1em}}.swal2-popup.swal2-toast{box-shadow:0 0 0.625em rgba(0,0,0,0.2) !important}.swal2-popup.swal2-toast .swal2-content{margin-left:7px !important}

